$bg-img: url('../../assets/contact_background.webp');

.contact {
  h1 {
    padding: 15px 0;
    background-color: #5c2c00;
    text-align: center;
    font-size: 60px;
    margin: 0;
    color: white;
  }

  section {
    background-image: $bg-img;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 50px;
    max-width: 1920px;
    height: 820px;

    article:first-child {
      text-align: center;

      h2 {
        font-size: 54px;
      }

      p {
        font-size: 32px;
      }

      h2, p {
        opacity: 0.7;
        margin: 0;
      }
    }

    article:last-child {
      height: max-content;
      width: 100%;
      max-width: 580px;

      form {
        display: flex;
        flex-direction: column;
        gap: 25px;

        label {
            font-size: 20px;
            font-weight: bold;
        }

        input {
            border: none;
            border-bottom: solid 1px black;
            background: none;
            font-size: 20px;
        }

        textarea {
            border: none;
            border-bottom: solid 1px black;
            background: none;
            font-size: 18px;
            height: 120px;
        }

        button {
            border: none;
            width: 30%;
            background-color: #864000;
            color: white;
            padding: 10px 20px;
            border-radius: 15px;
            font-size: 24px;
            align-self: flex-end;
            transition: all 0.5s ease-in-out;

            &:hover {
                background-color: #5b2b00;
                cursor: pointer;
            }
        }
      }
    }

    span {
        height: 80%;
        border: solid 1px black;
    }
  }


}

.validation {
  font-size: 28px;
  color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #864000;
  padding: 15px;
  border-radius: 10px;
  display: flex;
}

.invisible {
  display: none;
}